:root {
   
    --black-lightness: 15%;
    --dark-lightness: 44%;
    --light-lightness: 94%;
    --white-lightness: 100%;
    --color-primary: hsl(var(--primary-hue), 89%, 41%);
    --color-white: hsl(0, 5%, var(--white-lightness));
    --color-light: hsl(var(--primary-hue), 5%, var(--light-lightness));
    --color-black: hsl(var(--primary-hue), 5%, var(--black-lightness));
    --color-dark: hsl(var(--primary-hue), 5%, var(--dark-lightness));

    /* GENERAL CONTAINER VARIABLES */
    --container-width-lg: 76%;
    --container-width-md: 90%;

    /* BORDER RADIUS VARIABLES */
    --border-radius-1: 1.2rem;
    --border-radius-2: 0.8rem;
    --border-radius-3: 0.5rem;

    /* GENERAL TRANSITION VARIABLE */
    --transition: all 500ms ease;
}

html {
    scroll-behavior: smooth;
}













/* THEME change using react */
main.color-1 {
    --primary-hue: 270;
}
main.color-2 {
    --primary-hue: 110;
}
main.color-3 {
    --primary-hue: 240;
}
main.color-4 {
    --primary-hue: 325;
}
main.color-5 {
    --primary-hue: 0;
}
main.color-6 {
    --primary-hue: 55;
}

main.bg-2 {
    --black-lightness: 96%;
    --dark-lightness: 60%;
    --light-lightness: 12%;
    --white-lightness: 6%;
    --color-white: hsl(0, 5%, var(--white-lightness));
    --color-light: hsl(var(--primary-hue), 5%, var(--light-lightness));
    --color-black: hsl(var(--primary-hue), 5%, var(--black-lightness));
    --color-dark: hsl(var(--primary-hue), 5%, var(--dark-lightness));
}

main {
    color: hsl(var(--primary-hue), 5%, 44%);
    color: var(--color-dark);
    --color-primary: hsl(var(--primary-hue), 89%, 41%);
}








/* GENERAL CONTAINER CLASS */
.container {
    width: 76%;
    width: var(--container-width-lg);
    margin-inline: auto;
    max-width: 1920px;
}

/* GENERAL SECTION STYLES */
section {
    padding: 4rem 0;
    background: hsl(var(--primary-hue), 5%, 94%);
    background: var(--color-light);
    transition: all 500ms ease;
    transition: var(--transition);
}

section:nth-child(even) {
    background: hsl(0, 5%, 100%);
    background: var(--color-white);
}

section > h2 {
    text-align: center;
}

section > p {
    text-align: center;
    margin: 0.5rem auto 4rem;
    width: 40%;
}


/* GENERAL SECTION STYLES */
a {
    /* color: var(--color-white); */
    transition: all 500ms ease;
    transition: var(--transition);
}

/* GENERAL HEADING STYLES */
h1, h2, h3, h4, h5, h6 {
    line-height: 1.1;
    color: hsl(var(--primary-hue), 5%, 15%);
    color: var(--color-black);
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.6rem;
}

h4 {
    font-size: 1.1rem;
}

h5 {
    font-size: 0.95rem;
}

h6 {
    font-size: 0.8rem;
}



/* GENERAL BUTTON STYLES */
.btn {
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    background: hsl(var(--primary-hue), 5%, 94%);
    background: var(--color-light);
    color: hsl(var(--primary-hue), 5%, 15%);
    color: var(--color-black);
    padding: 0.6rem 2.5rem;
    border: 2px solid transparent;
    border-radius: 0.8rem;
    border-radius: var(--border-radius-2);
    cursor: pointer;
}

.btn:hover {
    background-color: transparent;
    border-color: hsl(var(--primary-hue), 5%, 94%);
    border-color: var(--color-light);
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem);
}

.btn.white {
    background: hsl(0, 5%, 100%);
    background: var(--color-white);
}

.btn.white:hover {
    background: transparent;
    border-color: hsl(0, 5%, 100%);
    border-color: var(--color-white);
}

.btn.primary {
    background: hsl(var(--primary-hue), 89%, 41%);
    background: var(--color-primary);
    /* text color always white that's why i didn't use a variable color */
    color: white;
}

.btn.primary:hover {
    background: transparent;
    border-color: hsl(var(--primary-hue), 89%, 41%);
    border-color: var(--color-primary);
    color: hsl(var(--primary-hue), 89%, 41%);
    color: var(--color-primary);
}

.btn.sm {
    padding: 0.5rem 1.2rem;
}




/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    body {
        font-size: 1rem;
    }
    
    .container {
        width: 90%;
        width: var(--container-width-md);
    }

    section {
        padding: 4rem 0;
    }

    section > p {
        width: 72%;
        margin-bottom: 3rem;
    }

    h1 {
        font-size: 2rem;
    }
}





/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {
    h1 {
        font-size: 2.4rem;
    }

    h2 {
        font-size: 1.8rem;
    }

    h3 {
        font-size: 1.4rem;
    }

    h4 {
        font-size: 1rem;
    }

    .btn {
        padding: 0.6rem 1.5rem;
    }

    section > p {
        width: 90%;
        width: var(--container-width-md);
    }
}





