@import url("https://fonts.googleapis.com/css?family=Josefin+Sans|Kalam|Kumar+One+Outline|Viga|Londrina+Shadow&display=swap");
body {
  margin: 0;
  font-family: 'Josefin Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
  text-align: center;
  overflow-x: hidden; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 500; }

img {
  max-width: 100%; }

.flex-row-center {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }

.flex-column-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.flex-row-space-between {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }

.flex-row-space-around {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center; }

.flex-row-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }

.row {
  width: 100%; }

.contact-row {
  display: flex;
  align-items: center; }

.col-2 {
  display: inline-grid;
  width: 50%;
  padding: 15px; }

@media only screen and (max-width: 700px) {
  .col-2 {
    width: 100%; }
  .contact-row {
    display: block; } }
